/* ====== STRUTTURA GENERALE ====== */
.scommesse-page {
  min-height: 100vh;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

.Navbar-scommesse {
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 100;
}

/* contenuto principale sotto la navbar */
.scommesse-content {
  margin-top: 100px; /* spazio per la navbar */
  padding: 40px 40px 80px;
  width: 100%;
  max-width: 1400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  scrollbar-width: none; /* Firefox */
}
.scommesse-content::-webkit-scrollbar {
  display: none; /* Chrome / Edge / Safari */
}

/* ====== TITOLO ====== */
.page-title {
  font-size: 2rem;
  color: #111;
  font-weight: 700;
  margin-bottom: 40px;
  text-align: center;
}

/* ====== GRIGLIA ====== */
.schedine-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 35px;
  width: 100%;
  justify-items: center;
}

/* ====== CARD SCHEDINA ====== */
.schedina-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 14px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  padding: 18px;
  width: 100%;
  max-width: 300px;
  cursor: pointer;
  transition: all 0.25s ease;
}
.schedina-card:hover {
  transform: scale(1.04);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
}

.schedina-card h3 {
  color: #e60000;
  font-weight: 700;
  margin-bottom: 10px;
}
.schedina-card ul {
  list-style: none;
  padding: 0;
  margin: 0 0 10px 0;
}
.schedina-card li {
  border-bottom: 1px solid #eee;
  padding: 4px 0;
  font-size: 0.95rem;
  color: #333;
}
.schedina-card li:last-child {
  border-bottom: none;
}
.schedina-card p {
  margin: 6px 0;
  font-size: 0.9rem;
  color: #444;
}
.timestamp {
  font-size: 0.8rem;
  color: #777;
  text-align: right;
}

/* ====== OVERLAY E MODAL ====== */
body.modal-open {
  overflow: hidden; /* blocca lo scroll sotto il modal */
}

.overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.3s ease forwards;
}

/* finestra zoom schedina */
.schedina-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  background: #fff;
  border-radius: 16px;
  padding: 25px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 400px;
  z-index: 1000;
  animation: zoomIn 0.25s ease forwards;
  max-height: 90vh;
  overflow-y: auto;
}

.schedina-modal h3 {
  color: #e60000;
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 15px;
}

.modal-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  padding: 6px 0;
  font-size: 0.95rem;
}
.modal-item:last-child {
  border-bottom: none;
}

.modal-info {
  font-size: 0.9rem;
  margin-top: 15px;
  text-align: left;
}

.close-btn {
  margin-top: 20px;
  width: 100%;
  background: #e60000;
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 10px 0;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.25s ease;
}
.close-btn:hover {
  background: #b00000;
}

/* ====== ANIMAZIONI ====== */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes zoomIn {
  from { transform: translate(-50%, -50%) scale(0.8); opacity: 0; }
  to { transform: translate(-50%, -50%) scale(1); opacity: 1; }
}

/* ====== RESPONSIVE ====== */
@media (max-width: 768px) {
  .schedine-grid {
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 25px;
  }
  .schedina-card {
    max-width: 250px;
  }
}
