@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/* ====== RESET BASE ====== */
body {
  margin: 0;
  background-color: #ffffff;
  font-family: "Poppins", sans-serif;
}

button {
  cursor: pointer;
}

img {
  user-select: none;
}

/* ====== SCHERMATA DI AVVIO ====== */
.start-screen {
  height: 100vh;
  width: 100vw;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.start-logo {
  width: 200px;
  height: auto;
}

