.navbar {
  background-color: #000;
  border-bottom: 3px solid #e00000;
  color: #fff;
  font-family: "Poppins", sans-serif;
  position: sticky;
  top: 0;
  z-index: 1000;
  padding: 2.5rem;
}

/* Contenitore principale */
.navbar-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

/* Logo centrato perfettamente */
.navbar-logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.navbar-logo img {
  height: 100px;
  display: block;
  filter: drop-shadow(0 0 3px rgba(255, 255, 255, 0.562));
  transition: opacity 0.3s ease; /* solo effetto leggero */
}

.navbar-logo img:hover {
  opacity: 0.9; /* NON si muove pi√π, solo effetto visivo */
}

/* Link */
.navbar-links {
  display: flex;
  list-style: none;
  gap: 2rem;
  padding: 0;
  margin: 0;
}

.navbar-left {
  margin-right: auto;
}

.navbar-right {
  margin-left: auto;
}

.navbar-links a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  font-size: 1rem;
  position: relative;
  transition: color 0.3s ease;
}

.navbar-links a::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0%;
  height: 2px;
  background: #e00000;
  transition: width 0.3s ease;
}

.navbar-links a:hover {
  color: #e00000;
}

.navbar-links a:hover::after,
.navbar-links a.active::after {
  width: 100%;
}

.navbar-links a.active {
  color: #e00000;
}

/* Responsive */
@media (max-width: 768px) {
  .navbar-inner {
    flex-direction: column;
    gap: 0.5rem;
  }

  .navbar-logo {
    position: static;
    transform: none;
    margin: 10px 0;
  }

  .navbar-links {
    justify-content: center;
  }
}
