.home-container {
  background: white;
  color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}

.home-content {
  display: flex;
  gap: 2rem;
  padding: 2rem;
  justify-content: space-between;
}

/* ====== TABELLA INCONTRI ====== */
.matches-section {
  flex: 0.7;
  max-height: 80vh;
  overflow-y: auto;
  scrollbar-width: none;
}
.matches-section::-webkit-scrollbar {
  display: none;
}

.section-title {
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.section-title span {
  color: gray;
  font-weight: 400;
}

.matches-table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  overflow: hidden;
}

.matches-table thead {
  background-color: #f4f4f4;
  color: #444;
  position: sticky;
  top: 0;
  z-index: 1;
}

.matches-table th, 
.matches-table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #eee;
  font-size: 0.95rem;
}

.matches-table tr:hover {
  background: #f9f9f9;
}

.fighters {
  display: flex;
  flex-direction: column;
}

.fighter1 {
  font-weight: 600;
}

.fighter2 {
  color: gray;
  font-size: 0.9rem;
}

.odd-btn {
  background: #f2f2f2;
  padding: 6px 12px;
  border-radius: 6px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: 0.3s;
}

.odd-btn:hover {
  background: #e5e5e5;
}

.odd-btn.active {
  background: #e00000;
  color: #fff;
}

/* ====== SCHEDINA ====== */
.betslip-box {
  flex: 0.3;
  position: sticky;
  top: 100px;
  background: #fafafa;
  border: 1px solid #ddd;
  border-radius: 15px;
  padding: 1rem;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
  max-height: 80vh;
  overflow-y: auto;
  scrollbar-width: none;
}
.betslip-box::-webkit-scrollbar {
  display: none;
}

.betslip-title {
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.betslip-empty {
  text-align: center;
  color: gray;
}

.betslip-empty .hint {
  font-size: 0.85rem;
  margin-top: 4px;
}

.betslip-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.betslip-item {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 10px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.2s;
}

.betslip-item:hover {
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.betslip-info {
  flex: 1;
  overflow: hidden;
}

.betslip-header {
  display: flex;
  justify-content: space-between;
  color: gray;
  font-size: 0.8rem;
}

.category {
  display: flex;
  align-items: center;
  gap: 3px;
}

.category .label {
  font-weight: 600;
  color: #0056b3;
}

.fighters-line {
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #000;
}

.fighters-line .vs {
  color: gray;
  font-weight: 400;
  margin: 0 4px;
}

.betslip-actions {
  display: flex;
  align-items: center;
  gap: 10px;
  border-left: 1px solid #ddd;
  padding-left: 10px;
}

.trash {
  color: #666;
  font-size: 0.8rem;
  cursor: pointer;
  transition: color 0.3s;
}

.trash:hover {
  color: #e00000;
}

.odd {
  font-weight: 600;
}

.place-bet {
  width: 100%;
  background: #e00000;
  color: #fff;
  font-weight: 600;
  padding: 12px;
  border: none;
  border-radius: 10px;
  margin-top: 1rem;
  cursor: pointer;
  transition: background 0.3s;
}

.place-bet:hover {
  background: #b00000;
}

/* ====== IMPORTO BLOCCATO + TOTALE ====== */
.betslip-amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  padding: 8px 10px;
  border-top: 1px solid #ddd;
}

.betslip-amount span {
  font-weight: 600;
  color: #333;
}

.betslip-amount input {
  width: 80px;
  text-align: right;
  border: none;
  background: transparent;
  font-weight: 600;
  color: #000;
  font-size: 0.9rem;
}

.betslip-total {
  text-align: right;
  margin-top: 5px;
  font-size: 0.9rem;
  color: #333;
  font-weight: 500;
}

.betslip-total span {
  font-weight: 700;
  color: #e00000;
}

/* ====== üîπ RESPONSIVE MOBILE ====== */
@media (max-width: 768px) {
  .home-content {
    flex-direction: column; /* tabella sopra, schedina sotto */
    padding: 1rem;
    gap: 1.5rem;
  }

  .matches-section {
    max-height: none;
    overflow-y: visible;
  }

  .matches-table th,
  .matches-table td {
    padding: 10px 8px;
    font-size: 0.85rem;
  }

  .betslip-box {
    position: relative;
    top: 0;
    max-height: none;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
  }

  .betslip-title {
    font-size: 1rem;
  }

  .place-bet {
    padding: 10px;
    font-size: 0.9rem;
  }
}
